<template>
  <div>
    <Row>
      <Col>
        <Menu theme="dark" :open-names="['1', '2']" @on-select="onSelect">
          <Submenu name="1">
            <template slot="title">
              <Icon type="ios-paper"></Icon>
              
            </template>
            <Menu-item name="1-1"></Menu-item>
            <Menu-item name="1-2"></Menu-item>
            <Menu-item name="1-3"></Menu-item>
          </Submenu>
          <Submenu name="2">
            <template slot="title">
              <Icon type="ios-people"></Icon>
              
            </template>
            <Menu-item name="2-1"></Menu-item>
            <Menu-item name="2-2"></Menu-item>
          </Submenu>
        </Menu>
      </Col>
    </Row>
  </div>
</template>
<script>
  export default {
    name: 'menu',
    methods: {
      onSelect (name) {
        this.$router.push(name)
      }
    }
  }
</script>



